<script>
// Clock import
import moment from "moment";
import VueClock from "@dangvanthanh/vue-clock";

import { translateAll, langs } from "../js/backend.js";
import { getItems, getLatestOrderId } from "../js/backend.js";
import { getLatestToppingUUID } from "../js/backend.js";
import { getLatestItemUUID } from "../js/backend.js";
import { insertOrder } from "../js/backend.js";
import { insertOrderItems } from "../js/backend.js";
import { insertOrderToppings } from "../js/backend.js";
import { getIdFromName } from "../js/backend.js";
import { incrementInventory } from "../js/backend.js";
import { getItemsFromCategory } from "../js/backend.js";
import { getToppings } from "../js/backend.js";
import { loadGoogle, userSignedIn } from "../js/login.js";

export default {
  data: () => ({
    colorblind: false,
    colorblindButton: false,
    langs: [],
    count: -1,
    enable_toppings: false,
    enable_entrees: true,
    enalbe_mainProteins: false,
    enable_subProteins: false,
    enable_delete: false,
    tip: 0,
    sub_total: 0,
    total: 0,
    calories_total: 0,
    tip1: false,
    tip2: false,
    tip3: false,
    tip4: false,
    light_mode: false,
    dark_mode: true,
    flag_delete: false,
    current_time: moment(new Date()).format("YYYY-MM-DD hh:mm:ss"),
    topping_count: 0,
    mainProtein_count: 0,
    subProtein_count: 0,
    latestOrderID: 0,
    latestToppingUUID: 0,
    latestItemUUID: 0,

    justify: ["space-between"],

    headers: [
      {
        text: "NUMBER",
        align: "start",
        sortable: false,
        value: "number",
      },
      { text: "NAME", value: "name", sortable: false },
      { text: "PRICE", value: "price", sortable: false },
      { text: "DELETE", value: "delete", sortable: false },
    ],

    items: [],
    entrees_array: [],
    toppings_array: [],
    editedIndex: -1,
    all_items: [],
    mainEntrees_all: [],
    mainEntrees_name: [],
    subEntrees_name: [],
    subEntrees_all: [],
    mainProteins_name: [],
    mainProteins_all: [],
    subProteins_name: [],
    subProteins_all: [],
    toppings_name: [],
    toppings_all: [],
    signedIn: false,
    spacer: false,
  }), // data end

  computed: {
    /**
     * it calculates total amount
     * @computed
     * @returns {sub_total} total amount
     */
    calculate_subTotal() {
      let sum = 0;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].price;
      }
      this.sub_total_track = sum;
      return (this.sub_total = sum);
    },

    /**
     * it returns item count
     * @computed
     * @returns {count} item count
     */
    countc() {
      return this.count;
    },

    /**
     * it returns a boolean variable of enable_toppings
     * @computed
     * @returns {enable_toppings} boolean variable
     */
    isDisabled() {
      return this.enable_toppings;
    },

    /**
     * it returns a boolean variable of isDisabled_mainProteins
     * @computed
     * @returns {enalbe_mainProteins} boolean variable
     */
    isDisabled_mainProteins() {
      return this.enalbe_mainProteins;
    },

    /**
     * it returns a boolean variable of isDisabled_subProteins
     * @computed
     * @returns {enable_subProteins} boolean variable
     */
    isDisabled_subProteins() {
      return this.enable_subProteins;
    },

    /**
     * it returns a boolean variable of isDisabled_entrees
     * @computed
     * @returns {enable_entrees} boolean variable
     */
    isDisabled_entrees() {
      return this.enable_entrees;
    },

    /**
     * it returns a boolean variable of isDisabled_entrees
     * @computed
     * @returns {enable_entrees} boolean variable
     */
    isDisabled_delete() {
      return this.enable_delete;
    },

    /**
     * it returns a boolean variable of tip1
     * @computed
     * @returns {tip1} boolean variable
     */
    istip1() {
      return this.tip1;
    },

    /**
     * it returns a boolean variable of tip2
     * @computed
     * @returns {tip2} boolean variable
     */
    istip2() {
      return this.tip2;
    },

    /**
     * it returns a boolean variable of tip3
     * @computed
     * @returns {tip3} boolean variable
     */
    istip3() {
      return this.tip3;
    },

    /**
     * it returns a boolean variable of tip4
     * @computed
     * @returns {tip4} boolean variable
     */
    istip4() {
      return this.tip4;
    },

    /**
     * it calculates 5% tip of the total amount
     * @computed
     * @returns {tip} tip amount
     */
    calculate_tip_1() {
      let sum = 0;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].price;
      }
      this.tip = sum * 0.05;
      return this.tip;
    },

    /**
     * it calculates 10% tip of the total amount
     * @computed
     * @returns {tip} tip amount
     */
    calculate_tip_2() {
      let sum = 0;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].price;
      }
      this.tip = sum * 0.1;
      return this.tip;
    },

    /**
     * it calculates 15% tip of the total amount
     * @computed
     * @returns {tip} tip amount
     */
    calculate_tip_3() {
      let sum = 0;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].price;
      }
      this.tip = sum * 0.15;
      return this.tip;
    },

    /**
     * it calculates 20% tip of the total amount
     * @computed
     * @returns {tip} tip amount
     */
    calculate_tip_4() {
      let sum = 0;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].price;
      }
      this.tip = sum * 0.2;
      return this.tip;
    },

    /**
     * it calculates total amount price
     * @computed
     * @returns {calculate_total} total price
     */
    calculate_total() {
      let sum = 0;
      for (let i = 0; i < this.items.length; i++) {
        sum += this.items[i].price;
      }
      this.total = (sum + this.tip).toFixed(2);
      return (sum + this.tip).toFixed(2);
    },

    /**
     * it calculates total calories
     * @computed
     * @returns {calories_total} total calories of order
     */
    calculate_total_calories() {
      return this.calories_total;
    },
  },

  // watch
  watch: {},

  /**
   * it is a clock component
   */
  components: { VueClock },

  // methods
  methods: {
    toggleColorblind() {
      let main = document.getElementsByTagName("main").item(0);
      if (this.colorblind) {
        main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117))`;
        this.colorblindButton = true;
      } else {
        main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117)), url(${require("./background.jpg")})`;
        this.colorblindButton = false;
      }
      localStorage.setItem("colorblind", JSON.stringify(this.colorblind));
    },

    /**
     * it reloads the page and go to the client page
     * @method
     */
    reloadPage() {
      window.location.reload();
    },

    /**
     * it deletes a specific item in the table
     * @method
     * @param {Item} item - Item object
     */
    async deleteItem(item) {
      this.editedIndex = this.items.indexOf(item);

      // if item.name is Entrees
      for (let a = 0; a < this.mainEntrees_all.length; a++) {
        if (item.name === this.mainEntrees_all[a].name) {
          this.flag_delete = false;
          var q = 0;
          while (q < this.items.length) {
            if (item.number === this.items[q].number) {
              this.remove_calories(this.items[q].name);
              this.items.splice(q, 1);
            } else {
              ++q;
            }
          }
          // splice from toppings_array
          var r = 0;
          while (r < this.toppings_array.length) {
            if (this.toppings_array[r].number === item.number) {
              this.toppings_array.splice(r, 1);
            } else {
              ++r;
            }
          }
          // splice from entrees_array
          for (let s = 0; s < this.entrees_array.length; s++) {
            if (item.number === this.entrees_array[s].number) {
              this.entrees_array.splice(s, 1);
            }
          }

          if (this.items.length === 0) {
            this.enable_entrees = true;
            this.count = -1;
            this.topping_count = 0;
            this.mainProtein_count = 0;
            this.subProtein_count = 0;
          }

          this.remove_count();
          this.remove_topping_count();
          this.remove_mainProtein_count();
          this.remove_subProtein_count();
          this.enable_toppings_button(this.topping_count);
          this.e_mainProtein_button(this.mainProtein_count);
          this.e_subProtein_button(this.subProtein_count);
        }
      }

      // if item.name is mainProtein
      for (let x = 0; x < this.mainProteins_name.length; x++) {
        if (item.name === this.mainProteins_name[x]) {
          this.enable_entrees = false;
          this.flag_delete = true;
          this.remove_calories(item.name);
          this.mainProtein_count -= 1;
          for (let b = 0; b < this.items.length; b++) {
            if (
              item.name === this.items[b].name &&
              item.number === this.items[b].number
            ) {
              this.items.splice(b, 1);
            }
          }
          for (let y = 0; y < this.toppings_array.length; y++) {
            if (
              item.name === this.toppings_array[y].name &&
              item.number === this.toppings_array[y].number
            ) {
              this.toppings_array.splice(y, 1);
            }
          }
          // check mainProtein
          this.mainProtein_count = 0;
          for (let a = 0; a < this.items.length; a++) {
            if (this.items[a].number === item.number) {
              for (let b = 0; b < this.mainProteins_name.length; b++) {
                if (this.items[a].name === this.mainProteins_name[b]) {
                  this.mainProtein_count += 1;
                }
              }
            }
          }
          // check subProtein
          this.subProtein_count = 0;
          for (let e = 0; e < this.items.length; e++) {
            if (this.items[e].number === item.number) {
              for (let f = 0; f < this.subProteins_name.length; f++) {
                if (this.items[e].name === this.subProteins_name[f]) {
                  this.subProtein_count += 1;
                }
              }
            }
          }
          // check topping
          this.topping_count = 0;
          for (let c = 0; c < this.items.length; c++) {
            if (this.items[c].number === item.number) {
              for (let d = 0; d < this.toppings_name.length; d++) {
                if (this.items[c].name === this.toppings_name[d]) {
                  this.topping_count += 1;
                }
              }
            }
          }
          this.enable_toppings_button(this.topping_count);
          this.e_mainProtein_button(this.mainProtein_count);
          this.e_subProtein_button(this.subProtein_count);
        }
      }

      // if item.name is subProtein
      for (let y = 0; y < this.subProteins_name.length; y++) {
        if (item.name === this.subProteins_name[y]) {
          this.enable_entrees = false;
          this.flag_delete = true;
          this.remove_calories(item.name);
          this.subProtein_count -= 1;
          for (let b = 0; b < this.items.length; b++) {
            if (
              item.name === this.items[b].name &&
              item.number === this.items[b].number
            ) {
              this.items.splice(b, 1);
            }
          }
          for (let z = 0; z < this.toppings_array.length; z++) {
            if (
              item.name === this.toppings_array[z].name &&
              item.number === this.toppings_array[z].number
            ) {
              this.toppings_array.splice(z, 1);
            }
          }
          // check mainProtein
          this.mainProtein_count = 0;
          for (let a = 0; a < this.items.length; a++) {
            if (this.items[a].number === item.number) {
              for (let b = 0; b < this.mainProteins_name.length; b++) {
                if (this.items[a].name === this.mainProteins_name[b]) {
                  this.mainProtein_count += 1;
                }
              }
            }
          }
          // check subProtein
          this.subProtein_count = 0;
          for (let e = 0; e < this.items.length; e++) {
            if (this.items[e].number === item.number) {
              for (let f = 0; f < this.subProteins_name.length; f++) {
                if (this.items[e].name === this.subProteins_name[f]) {
                  this.subProtein_count += 1;
                }
              }
            }
          }
          // check topping
          this.topping_count = 0;
          for (let c = 0; c < this.items.length; c++) {
            if (this.items[c].number === item.number) {
              for (let d = 0; d < this.toppings_name.length; d++) {
                if (this.items[c].name === this.toppings_name[d]) {
                  this.topping_count += 1;
                }
              }
            }
          }
          this.enable_toppings_button(this.topping_count);
          this.e_mainProtein_button(this.mainProtein_count);
          this.e_subProtein_button(this.subProtein_count);
        }
      }

      // if item.name is toppings
      for (let z = 0; z < this.toppings_name.length; z++) {
        if (item.name === this.toppings_name[z]) {
          this.enable_entrees = false;
          this.flag_delete = true;
          this.remove_calories(item.name);
          this.topping_count -= 1;
          for (let b = 0; b < this.items.length; b++) {
            if (
              item.name === this.items[b].name &&
              item.number === this.items[b].number
            ) {
              this.items.splice(b, 1);
              break;
            }
          }
          for (let k = 0; k < this.toppings_array.length; k++) {
            if (
              item.name === this.toppings_array[k].name &&
              item.number === this.toppings_array[k].number
            ) {
              this.toppings_array.splice(k, 1);
            }
          }
          // check mainProtein
          this.mainProtein_count = 0;
          for (let a = 0; a < this.items.length; a++) {
            if (this.items[a].number === item.number) {
              for (let b = 0; b < this.mainProteins_name.length; b++) {
                if (this.items[a].name === this.mainProteins_name[b]) {
                  this.mainProtein_count += 1;
                }
              }
            }
          }
          // check subProtein
          this.subProtein_count = 0;
          for (let e = 0; e < this.items.length; e++) {
            if (this.items[e].number === item.number) {
              for (let f = 0; f < this.subProteins_name.length; f++) {
                if (this.items[e].name === this.subProteins_name[f]) {
                  this.subProtein_count += 1;
                }
              }
            }
          }
          // check topping
          this.topping_count = 0;
          for (let c = 0; c < this.items.length; c++) {
            if (this.items[c].number === item.number) {
              for (let d = 0; d < this.toppings_name.length; d++) {
                if (this.items[c].name === this.toppings_name[d]) {
                  this.topping_count += 1;
                }
              }
            }
          }
          this.enable_toppings_button(this.topping_count);
          this.e_mainProtein_button(this.mainProtein_count);
          this.e_subProtein_button(this.subProtein_count);
        }
      }
      // check blank
      if (item.name === "") {
        for (let i = 0; i < this.items.length; i++) {
          if (item.number === this.items[i].number && item.name === this.items[i].name) {
            this.items.splice(i, 1);
          }
        }
      }
    },

