    /**
     * It decides what tip button the user pressesd
     * @method
     * @param {number} k - number
     */
    tip_control(k) {
      if (k === 1) {
        this.tip1 = true;
        this.tip2 = false;
        this.tip3 = false;
        this.tip4 = false;
      } else if (k === 2) {
        this.tip1 = false;
        this.tip2 = true;
        this.tip3 = false;
        this.tip4 = false;
      } else if (k === 3) {
        this.tip1 = false;
        this.tip2 = false;
        this.tip3 = true;
        this.tip4 = false;
      } else if (k === 4) {
        this.tip1 = false;
        this.tip2 = false;
        this.tip3 = false;
        this.tip4 = true;
      }
    },

