        if (e[u].name != null) {
          this.toppings_name.push(e[u].name);
        }
      }
    },

    /**
     * it places an order
     * @async
     * @method
     */
    async insertOrderTable() {
      this.latestOrderID = await getLatestOrderId();
      this.latestItemUUID = await getLatestItemUUID();
      this.latestToppingUUID = await getLatestToppingUUID();

      await insertOrder(
        this.latestOrderID + 1,
        this.calories_total,
        this.sub_total,
        this.tip,
        this.total,
        this.current_time
      );

      try {
        let c = 1;
        if (this.entrees_array != []) {
          for (let i = 0; i < this.entrees_array.length; i++) {
            let id_entrees = await getIdFromName(this.entrees_array[i].name);
            while (id_entrees == null) {
              id_entrees = await getIdFromName(this.entrees_array[i].name);
            }
            await insertOrderItems(
              this.latestItemUUID + c,
              this.latestOrderID + 1,
              this.entrees_array[i].number,
              id_entrees,
              this.entrees_array[i].name
            );
            let inventroy_id_entrees = await getIdFromName(this.entrees_array[i].name);
            while (inventroy_id_entrees == null) {
              inventroy_id_entrees = await getIdFromName(this.entrees_array[i].name);
            }
            await incrementInventory(inventroy_id_entrees);
            c += 1;
          }
        }
        let d = 1;
        if (this.toppings_array != []) {
          for (let j = 0; j < this.toppings_array.length; j++) {
            let id_toppings = await getIdFromName(this.toppings_array[j].name);
            await insertOrderToppings(
              this.latestToppingUUID + d,
              this.latestOrderID + 1,
              this.toppings_array[j].number,
              id_toppings,
              this.toppings_array[j].name
            );
            let inventory_id_toppings = await getIdFromName(this.toppings_array[j].name);
            await incrementInventory(inventory_id_toppings);
            d += 1;
          }
        }
      } catch (error) {
        if (error.code == "23505") {
          alert("Error! unique key already exists.");
        } else {
          alert("Error! the server was disconnected.");
        }
        exit();
      }
      this.getData("Order Placed!");

      this.entrees_array = [];
      this.toppings_array = [];
      this.count = -1;
      this.latestOrderID = await getLatestOrderId();
      this.latestItemUUID = await getLatestItemUUID();
      this.latestToppingUUID = await getLatestToppingUUID();
      this.enalbe_mainProteins = false;
      this.enable_subProteins = false;
      this.enable_toppings = false;
      this.enable_entrees = true;
      this.tip = 0;
      this.total = 0;
      this.sub_total = 0;
      this.calories_total = 0;
      this.tip1 = false;
      this.tip2 = false;
      this.tip3 = false;
      this.tip4 = false;
      this.flag_delete = false;
      this.topping_count = 0;
      this.mainProtein_count = 0;
      this.subProtein_count = 0;
    },

    /**
     * it gets data from the database
     * @async
     * @method
     */
    async getItemInfo() {
      if (this.mainEntrees_all.length === 0) {
        this.mainEntrees_all = await getItems();
      } else {
        this.mainEntrees_all.splice(0);
        this.mainEntrees_all = await getItems();
      }

      if (this.mainProteins_all.length === 0) {
        this.mainProteins_all = await getItemsFromCategory("mainProtein");
      } else {
        this.mainProteins_all.splice(0);
        this.mainProteins_all = await getItemsFromCategory("mainProtein");
      }

      if (this.subProteins_all.length === 0) {
        this.subProteins_all = await getItemsFromCategory("subProtein");
        for (let i = 0; i < this.subProteins_all.length; i++) {
          this.mainProteins_all.push(this.subProteins_all[i]);
        }
      } else {
        this.subProteins_all.splice(0);
        this.subProteins_all = await getItemsFromCategory("subProtein");
        for (let i = 0; i < this.subProteins_all.length; i++) {
          this.mainProteins_all.push(this.subProteins_all[i]);
        }
      }

      if (this.toppings_all.length === 0) {
        this.toppings_all = await getItemsFromCategory("topping");
      } else {
        this.toppings_all.splice(0);
        this.toppings_all = await getItemsFromCategory("topping");
      }

      if (this.subEntrees_all.length === 0) {
        this.subEntrees_all = await getItemsFromCategory("subEntree");
      } else {
        this.subEntrees_all.splice(0);
        this.subEntrees_all = await getItemsFromCategory("subEntree");
      }
    },
  },

  /**
   * it calls the functions that belong.
   * @async
   * @created
   */
  async created() {
    this.langs = langs;
    await loadGoogle();
    this.signedIn = await userSignedIn(this.$route.params.credential);
    await this.updateItems();
    await this.getItemInfo();
    await this.make_all_items();
    /*await translateAll("es");*/
    this.countc();
  },

  /**
   * it calls the functions that belong.
   * @async
   * @mounted
   */
  async mounted() {
    if (JSON.parse(localStorage.getItem("colorblind"))) {
      this.colorblind = JSON.parse(localStorage.getItem("colorblind"));
    }
    this.toggleColorblind();
    await this.getOrderID();
    await this.getToppingUUID();
    await this.getItemUUID();
    await this.getIdFromName();
  },
};
</script>
