<template>
  <div>
    <div v-bind:style="heroImage">
      <v-container fluid>
        <v-row justify="space-between">
          <v-col>
            <v-switch
              v-model="colorblind"
              @change="toggleColorblind"
              label="Colorblind Mode"
            ></v-switch>
          </v-col>
          <v-col cols="auto">
            <v-btn
              v-if="$vuetify.breakpoint.mdAndUp"
              outlined
              @click="$router.push('login')"
              >Log In</v-btn
            >
          </v-col>
        </v-row>
        <v-row justify="center">
          <v-col cols="auto">
            <router-link
              :key="$route.path"
              :to="{ path: '/' }"
              style="text-decoration: none; color: inherit"
            >
              <v-img id="logo" src="./images/pom.png" width="250"></v-img>
            </router-link>
          </v-col>
        </v-row>
        <v-row justify="center" style="margin-top: 100px">
          <v-col cols="auto">
            <v-btn outlined large @click="$router.push('client')">Order Now</v-btn>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </div>
</template>

<script>
export default {
  name: "LandingView",

  components: {},
  computed: {
    heroImage() {
      return {
        backgroundImage: `url${require("./background.jpg")}`,
      };
    },
  },
};
</script>

<style>
main {
  background-size: auto;
}

#logo {
  margin-top: 3em;
}
</style>

<script>
import { loadGoogle, promptLogin, userSignedIn } from "../js/login.js";
export default {
  name: "LandingView",
  components: {},
  data: () => ({
    colorblind: false,
  }), // data end
  methods: {
    toggleColorblind() {
      let main = document.getElementsByTagName("main").item(0);
      if (this.colorblind) {
        main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117))`;
      } else {
        main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117)), url(${require("./background.jpg")})`;
      }
      localStorage.setItem("colorblind", JSON.stringify(this.colorblind));
    },

    /**
     * Prompts user to sign in
     * @async
     * @method
     */
    async signIn(e) {
      console.log(e);
      console.log(await userSignedIn(e.credential));
      await translate("test", "es", e.credential);
    },
  },

  /**
   * Initialize on startup
   * @async
   * @method
   */
  async mounted() {
    if (JSON.parse(localStorage.getItem("colorblind"))) {
      this.colorblind = JSON.parse(localStorage.getItem("colorblind"));
    }
    this.toggleColorblind();
    /*await loadGoogle();*/
    /*await promptLogin(this.signIn);*/
  },
};
</script>
