<template>
  <div>
    <div>
      <h2>275 Joe Routt Blvd - Memorial Student Center (MSC) </h2>
      <GmapAutocomplete
        @place_changed='setPlace'
      />
      <h3 v-if="distance && time">You are {{ distance }} and {{ time }} away</h3>
    </div>
    <br>
    <GmapMap
        :center='center'
        :zoom='12'
        style='width:100%;  height: 400px;'>
      <GmapMarker :key="index" v-for="(m, index) in markers" :position="m.position" :clickable="true" :draggable="true" @click="center=m.position"/>
      <DirectionsRenderer @data="getData" travelMode="DRIVING" :origin="origin" :destination="destination"/>
    </GmapMap>
  </div>
</template>

<script>
import DirectionsRenderer from '../js/DirectionsRenderer.js';
export default {
  components: { DirectionsRenderer },
  name: 'AddGoogleMap',
  data() {
    return {
        time: null,
        distance: null,
      center: { lat: 30.6123971, lng: -96.341474 },
      currentPlace: null,
      markers: [{
        position: {
          lat: 30.6123971,
          lng: -96.341474
        }
      }],
      end: { lat: 30.6123971, lng: -96.341474 },
      destination: null,
      origin: null,
      travelMode: "DRIVING",
    }
  },
  mounted() {
    this.geolocate();
  },
  methods: {
      getData(distance, time) {
        this.distance = distance;
        this.time = time;
      },
    setPlace(place) {
      this.currentPlace = new google.maps.LatLng( place.geometry.location.lat(), place.geometry.location.lng());
      this.origin = new google.maps.LatLng( place.geometry.location.lat(), place.geometry.location.lng());
      this.destination = new google.maps.LatLng(this.end.lat, this.end.lng);
    },
    geolocate() {
      navigator.geolocation.getCurrentPosition(position => {
        this.currentPlace = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        }
        this.origin = new google.maps.LatLng(this.currentPlace.lat,this.currentPlace.lng);
        this.destination = new google.maps.LatLng(this.end.lat, this.end.lng);
      });
    },
  },
}
</script>
