<template>
  <div>
    <v-container class="Top">
      <v-row justify="center" align="center">
        <v-col cols="6" md="auto">
          <v-btn @click.prevent="move()" small color="#4174D9" x-large
            >Add more Items</v-btn
          >
        </v-col>
        <v-col v-if="$vuetify.breakpoint.mdAndUp">
          <v-spacer />
        </v-col>
        <v-col cols="6" md="auto">
          Font Size
          <v-btn icon @click="fontIncrease" id="noTranslate"
            ><v-icon>mdi-plus</v-icon></v-btn
          >
          <v-btn icon @click="fontDecrease" id="noTranslate"
            ><v-icon>mdi-minus</v-icon></v-btn
          >
        </v-col>
        <v-col>
          <v-select
            label="Choose language"
            :items="langs"
            item-value="short"
            item-text="long"
            @change="translateHandle"
          ></v-select>
        </v-col>
      </v-row>
    </v-container>

    <v-container class="checkout">
      <v-form ref="checkoutForm">
        <v-row v-for="j in justify" :key="j" :justify="j">
          <!-- left -->
          <v-col v-for="k in 1" :key="k">
            <br /><br />
            <v-card class="pa-2">
              <center>
                <h1>Guest Information</h1>
              </center>
              <br />
            </v-card>
            <v-card class="pa-2">
              <v-row>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="firstName"
                    label="First Name"
                    clearable
                    :rules="[() => !!firstName || 'Required Field']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="lastName"
                    label="Last Name"
                    clearable
                    :rules="[() => !!lastName || 'Required Field']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="email"
                    label="Email"
                    clearable
                    type="email"
                    :rules="[() => !!email || 'Required Field']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="phone"
                    label="Mobile Phone"
                    clearable
                    type="phone"
                    :rules="[() => !!phone || 'Required Field']"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card>
            <br /><br />
            <v-card class="pa-2">
              <center>
                <h1>Payment Method</h1>
                <p class="payment">
                  Select a payment method below to continue with your order.
                </p>
              </center>
              <br />
            </v-card>
            <v-card class="pa-2">
              <v-row>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="creditCard"
                    label="Credit Card Number"
                    clearable
                    :rules="[() => !!creditCard || 'Required Field']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="expDate"
                    label="Exp. Date"
                    clearable
                    :rules="[() => !!expDate || 'Required Field']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="cvv"
                    label="CVV"
                    clearable
                    :rules="[() => !!cvv || 'Required Field']"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model="zip"
                    label="Zip Code"
                    clearable
                    :rules="[() => !!zip || 'Required Field']"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card>
            <br /><br />
            <v-card class="pa-2">
              <center>
                <AddGoogleMap />
              </center>
            </v-card>
          </v-col>
          <!-- right -->
          <v-col v-for="k in 1" :key="k">
            <br /><br />
            <v-card class="pa-2">
              <center>
                <h1>Your Order</h1>
                <v-col cols="12">
                  <div class="parent">
                    <div v-for="k in entrees_array" :key="k" class="son2">
                      <br />
                      <h2>#{{ k.number }} &nbsp; {{ k.name }} &nbsp;&nbsp;&nbsp;</h2>
                      <br />
                      <div v-for="r in priceEach" :key="r" class="son3">
                        <div v-if="k.number === r.number">
                          <p1>$ {{ r.price.toFixed(2) }}</p1>
                        </div>
                      </div>
                      <br />
                      <div v-for="q in toppings_array" :key="q" class="son">
                        <div v-if="k.number === q.number">
                          <center>
                            <p>- {{ q.name }} &nbsp;</p>
                          </center>
                        </div>
                      </div>
                    </div>
                  </div>
                </v-col>
              </center>
            </v-card>
            <br /><br />
            <v-card class="pa-10" inlined tile>
              <v-row>
                <v-col>
                  <p class="Entree">Subtotal:</p>
                </v-col>
                <right>
                  <v-col> $ {{ sub_total.toFixed(2) }} </v-col>
                </right>
              </v-row>
              <br />
              <center>
                <v-btn
                  :loading="loading"
                  :disabled="loading"
                  @click="insertOrderTable(), (loader = 'loading')"
                  color="#42a376"
                  x-large
                >
                  <div class="ps">Place Order</div>
                </v-btn>
              </center>
            </v-card>
          </v-col>
        </v-row>
      </v-form>
    </v-container>
  </div>
</template>

<script>
import moment from "moment";
import { getItems, getLatestOrderId } from "../js/backend.js";
import { getLatestToppingUUID } from "../js/backend.js";
import { getLatestItemUUID } from "../js/backend.js";
import { insertOrder } from "../js/backend.js";
import { insertOrderItems } from "../js/backend.js";
import { insertOrderToppings } from "../js/backend.js";
import { getIdFromName } from "../js/backend.js";
import { incrementInventory } from "../js/backend.js";
import { getItemsFromCategory } from "../js/backend.js";
import { getToppings } from "../js/backend.js";
import { translateAll, langs } from "../js/backend.js";
import AddGoogleMap from "../components/AddGoogleMap.vue";

export default {
  name: "ClientCheckoutView",

  /**
   * Google Map API components
   */
  components: {
    AddGoogleMap,
  },

  data: () => ({
    colorblind: false,
    loader: null,
    loading: false,
    firstName: null,
    lastName: null,
    order: [],
    email: null,
    phone: null,
    creditCard: null,
    expDate: null,
    cvv: null,
    zip: null,
    langs: [],
    sub_total: 0,
    total: 0,
    tax: 0,
    tip: 0,
    latestOrderID: 0,
    latestItemUUID: 0,
    latestToppingUUID: 0,
    calories_total: 0,
    toppings_array: [],
    entrees_array: [],
    orders_array: [],
    priceEach: [],
    current_time: moment(new Date()).format("YYYY-MM-DD hh:mm:ss"),

    justify: ["space-between"],
  }),

  watch: {
    /**
     * it is a place order button loader
     * @watch
     */
    loader() {
      const l = this.loader;
      this[l] = !this[l];
      if (this.$refs.checkoutForm.validate()) {
        setTimeout(() => (this[l] = false), 100000);
      } else {
        setTimeout(() => (this[l] = false), 1000);
      }
      this.loader = null;
    },
  },

  computed: {},

  /**
   * it calls the functions that belong.
   * @async
   * @created
   */
  async created() {
    this.langs = langs;
    if (JSON.parse(localStorage.getItem("colorblind"))) {
      this.colorblind = JSON.parse(localStorage.getItem("colorblind"));
    }

    this.sub_total = parseFloat(this.$route.query.sub_total);
    this.toppings_array = this.$route.query.toppings_array;
    this.entrees_array = this.$route.query.entrees_array;
    this.calories_total = parseInt(this.$route.query.calories_total);
    this.order = this.$route.query.order;
    this.priceEach = this.$route.query.priceEach;
  },

  /**
   * it calls the functions that belong.
   * @async
   * @mounted
   */
  async mounted() {
    /*await translateAll('es');*/
    this.toggleColorblind();
    await this.getOrderID();
    await this.getToppingUUID();
    await this.getItemUUID();
    await this.getIdFromName();
  },

  methods: {
    toggleColorblind() {
      let main = document.getElementsByTagName("main").item(0);
      if (this.colorblind) {
        main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117))`;
      } else {
        main.style.backgroundImage = `linear-gradient(to bottom, rgba(0, 0, 0, 0.0), rgb(42, 53, 117)), url(${require("./background.jpg")})`;
      }
      localStorage.setItem("colorblind", JSON.stringify(this.colorblind));
    },
    /**
     * it increases the font size
     * @method
     */
    fontIncrease() {
      const allElements = document.getElementsByTagName("*");
      for (const element of allElements) {
        let style = window.getComputedStyle(element, null).getPropertyValue("font-size");
        let currentSize = parseFloat(style);
        element.style.fontSize = currentSize + 2 + "px";
      }
    },

    /**
     * it decreases the font size
     * @method
     */
    fontDecrease() {
      const allElements = document.getElementsByTagName("*");
      for (const element of allElements) {
        let style = window.getComputedStyle(element, null).getPropertyValue("font-size");
        let currentSize = parseFloat(style);
        element.style.fontSize = currentSize - 2 + "px";
      }
    },

    /**
     * it is Google translate API
     * @method
     * @async
     */
    async translateHandle(e) {
      await translateAll(e);
    },

    /**
     * it goes to the client page with some queries
     * @method
     */
    move() {
      this.$router.push({
        path: "client",
      });
    },

    /**
     * it makes entrees_array empty
     * @method
     */
    empty() {
      this.entrees_array = [];
    },

    /**
     * it goes to the checkout complete page with one queriy
     * @method
     */
    completeOrder() {
      this.$router.push({
        path: "checkoutComplete",
        query: {
          latestOrderID: this.latestOrderID,
        },
      });
    },

    /**
     * it gets the latest order ID
     * @async
     * @method
     */
    async getOrderID() {
      this.latestOrderID = await getLatestOrderId();
    },

    /**
     * it gets the latest topping UUID
     * @async
     * @method
     */
    async getToppingUUID() {
      this.latestToppingUUID = await getLatestToppingUUID();
    },

    /**
     * it gets the latest item UUID
     * @async
     * @method
     */
    async getItemUUID() {
      this.latestItemUUID = await getLatestItemUUID();
    },

    /**
     * it places an order
     * @async
     * @method
     */
    async insertOrderTable() {
      if (this.$refs.checkoutForm.validate()) {
        this.latestOrderID = await getLatestOrderId();
        this.latestItemUUID = await getLatestItemUUID();
        this.latestToppingUUID = await getLatestToppingUUID();

        await insertOrder(
          this.latestOrderID + 1,
          this.calories_total,
          this.sub_total.toFixed(2),
          this.tip,
          this.sub_total,
          this.current_time
        );

        try {
          let c = 1;
          if (this.entrees_array != []) {
            for (let i = 0; i < this.entrees_array.length; i++) {
              let id_entrees = await getIdFromName(this.entrees_array[i].name);
              while (id_entrees == null) {
                id_entrees = await getIdFromName(this.entrees_array[i].name);
              }
              await insertOrderItems(
                this.latestItemUUID + c,
                this.latestOrderID + 1,
                this.entrees_array[i].number,
                id_entrees,
                this.entrees_array[i].name
              );
              let inventroy_id_entrees = await getIdFromName(this.entrees_array[i].name);
              while (inventroy_id_entrees == null) {
                inventroy_id_entrees = await getIdFromName(this.entrees_array[i].name);
              }
              await incrementInventory(inventroy_id_entrees);
              c += 1;
            }
          }

          let d = 1;
          if (this.toppings_array != []) {
            for (let j = 0; j < this.toppings_array.length; j++) {
              let id_toppings = await getIdFromName(this.toppings_array[j].name);
              await insertOrderToppings(
                this.latestToppingUUID + d,
                this.latestOrderID + 1,
                this.toppings_array[j].number,
                id_toppings,
                this.toppings_array[j].name
              );
              let inventory_id_toppings = await getIdFromName(
                this.toppings_array[j].name
              );
              await incrementInventory(inventory_id_toppings);
              d += 1;
            }
          }

          this.entrees_array.splice(0);
          this.toppings_array.splice(0);
          this.sub_total = 0;
          this.calories_total = 0;
          this.completeOrder();
        } catch (error) {
          alert("Error! the server was disconnected.");
          exit();
        }
      }
    },
  },
};
</script>

